import React, {Component} from 'react';
import {getSingleBill,getBranch} from '../functions/Api';
import { numberWithCommas, findWithAttr } from '../functions/Global';
import Logo from '../styles/resources/Logo.png';
import {RiArrowLeftSLine} from 'react-icons/ri';
import {Link} from 'react-router-dom';

class BillPrint extends Component {
    constructor(props) {
        super(props);

        this.state = {
            data: null,
            branch: null
        }

        getSingleBill(this.props.match.params.id, (res) => {
            this.setState({
                data:res
            })
        });

        getBranch((res) => {
            this.setState({
                branch: res
            })
        })
    }

    handlePrint = (e) => {
        e.target.style.display = 'none';
        this.refs.view_header.style.display = 'none';

        window.print()
    }

    render() {
        if(!this.state.data || !this.state.branch) {
            return <div></div>
        }

        let gioitinh = ["B Trai", "B Gi"]

        return(
            <div className="view bill_print">
                <h1 onClick={this.props.history.goBack} className="view_header" ref="view_header" style={{textDecoration: 'none',cursor: 'pointer',width: "100%", textAlign: "left", marginTop: "32px", marginBottom : "64px"}}><RiArrowLeftSLine className="icon"/>In Ha n</h1>
                <span style={{textAlign: "center", width: "100%", whiteSpace: "pre-wrap"}}>{findWithAttr(this.state.branch,'_id',parseInt(this.state.data.chi_nhanh)) > -1 ? this.state.branch[findWithAttr(this.state.branch,'_id',parseInt(this.state.data.chi_nhanh))].dia_chi : ''}</span>
                <h1>HA N</h1>
                <span>Chi Nhnh: <span>{findWithAttr(this.state.branch,'_id',parseInt(this.state.data.chi_nhanh)) > -1 ? this.state.branch[findWithAttr(this.state.branch,'_id',parseInt(this.state.data.chi_nhanh))].ten_chi_nhanh : ''}</span></span>
                <span>Ngy Chp: <span>{this.state.data.gio_chup} {this.state.data.ngay_chup}</span></span>
                <span>Ngy Giao Hnh: <span>{this.state.data.ngay_giao_hinh}</span></span>
                <span>Tn Khch Hng: <span>{this.state.data.ten_khach_hang}</span></span>
                <span>ST: <span>{this.state.data.so_dien_thoai}</span></span>
                <span>a Ch: <span>{this.state.data.dia_chi}</span></span>
                <span>H Tn B:  <span>{this.state.data.ho_ten_be}</span></span>
                <span>Ngy Sinh:  <span>{this.state.data.ngay_sinh_be}</span></span>
                <span>Gii Tnh: <span>{gioitinh[this.state.data.gioi_tinh_be]} - {this.state.data.so_tuoi}</span></span>

                <hr/>

                <table>
                    <tr>
                        <th>Sn Phm</th>
                        <th>n Gi</th>
                        <th>S Lng</th>
                        <th>Gim Gi</th>
                        <th>Tng</th>
                    </tr>
                    {this.state.data.products.map((data, i) => {
                        return(
                            <tr>
                                <td>{data.ten_goi}</td>
                                <td>{numberWithCommas(parseInt(data.gia))}</td>
                                <td>{data.so_luong}</td>
                                <td>{data.giam_gia ? data.giam_gia : 0} </td>
                                <td>{numberWithCommas(parseInt(data.gia) * parseInt(data.so_luong) - (data.giam_gia ? data.giam_gia : 0))} </td>
                            </tr>
                        )
                    })}
                </table>
                
                <hr/>

                <span>TNG CNG: <span>{numberWithCommas(parseInt(this.state.data.tong))} </span></span>
                <span>T CC: <span>{numberWithCommas(parseInt(this.state.data.dat_coc_1))} </span></span>
                <span>THANH TON LN 1: <span>{numberWithCommas(parseInt(this.state.data.thanh_toan_l1_1))} </span></span>
                <span>THANH TON LN 2: <span>{numberWithCommas(parseInt(this.state.data.thanh_toan_l2_1))} </span></span>
                <span>THANH TON LN 3: <span>{numberWithCommas(parseInt(this.state.data.thanh_toan_l3_1 ? this.state.data.thanh_toan_l3_1 : 0))} </span></span>
                <span>CN N: <span>{numberWithCommas(parseInt(this.state.data.con_no_1))} </span></span>

                <button className="btn blue" onClick={this.handlePrint}>IN</button>
            </div>
        )
    }
}

export default BillPrint;