import React,{Component} from 'react';
import {DefaultHeaderBar} from '../components/Header_bar';
import {StaffTable} from '../components/Table';
import {getPermissions, getStaff} from '../functions/Api';
import {removeVietnameseTones, findWithAttr} from '../functions/Global';
import {HashSpiner} from '../components/Spiners';

class Staff extends Component {
    constructor(props) {
        super(props);
        this.state = {
            perms: null,
            data: null
        } 

        getPermissions((res) => {
            this.setState({
                perms: res
            })
        })

        getStaff((res) => {
          this.setState ({
            data: res
          })
        });
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            getStaff((res) => {
                this.setState({
                    data: res
                })            
            })
        }

        //RELOAD DATA
        getStaff((res) => {
            let data = res.reverse()
  
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(removeVietnameseTones(data.email).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                if(removeVietnameseTones(data.ho_ten).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                
                return false;
            })
  
            this.setState({
                data: filtered_data
            })            
        })
    }

    updateData = () => {
        getStaff((res) => {
        this.setState ({
            data: res
        })
      })
    }

    render() {
        if(!this.state.perms || !this.state.data) {
            return(<div className="load_view"><HashSpiner/></div>)
        }
        
        return(
            <div>
                <DefaultHeaderBar onGoBack={() => this.props.history.goBack()} title="Nhn Vin" onSearchKeyPress={this.handleSearch} handleSave={null} handleRemove={null} handleCreate={null} create_btn_link={this.state.perms[findWithAttr(this.state.perms, '_id', localStorage.getItem('chuc_vu'))].permisisons.staff[1] == "1" ? "/staff_edit/create_new" : null }/>
                <StaffTable data={this.state.data} perms={this.state.perms}/>
            </div>
        )
    }
}

export default Staff